import { fetchHistoricPrice } from './btc/fetch-historic-price';
import { stringify } from './utils/stringify';
import { generateChart } from './highcharts/generate-chart';
import { SeriesObject } from 'highcharts';
import * as Highcharts from 'highcharts';

export function bootstrap() {
  const chart = generateChart('target', {
    chart: {
      zoomType: 'x'
    },
    title: {
      text: 'Bitcoin historic price'
    },
    xAxis: {
      type: 'datetime'
    },
    yAxis: {
      title: {
        text: 'price in EUR'
      }
    },
    legend: {
      enabled: false
    },
    plotOptions: {
      area: {
        fillColor: {
          linearGradient: {
              x1: 0,
              y1: 0,
              x2: 0,
              y2: 1
          },
          stops: [
              [0, Highcharts.getOptions().colors[0]],
              [1, Highcharts.Color(Highcharts.getOptions().colors[0])]
          ]
      },
      marker: {
          radius: 2
      },
      lineWidth: 1,
      states: {
          hover: {
              lineWidth: 1
          }
      },
      threshold: null
      }
    },
    series: [{}]
  });

  fetchHistoricPrice(new Date('2012-01-01'), new Date('2017-04-01'))
    .map(data => Object.keys(data.bpi).map(_ => ([Date.parse(_), Number(data.bpi[_])])))
    .subscribe(
      points => {
        console.log(points);
        chart.series[0].setData(points);
      },
      error => console.error(error)
    );

    // const points = data.timeseries.map(t => ([Date.parse(t.start), Number(t.value)]));
    // chart.update({ tooltip: { valueSuffix: ` ${data.unit}` }});
    // chart.series[index].update({name: `[${data.datapoint.dataPointId}] ${data.datapoint.name} (${data.unit})`});
    // if (options.mixin && options.mixin !== {}) {
    //     chart.update(options.mixin);
    // }
    // chart.series[index].setData(points);
}



