import { Observable } from 'rxjs';
import { fetchJson } from '../utils/fetch-json';
import * as utpl from 'uri-templates';
import * as moment from 'moment';

export function fetchHistoricPrice(from: Date, to: Date): Observable<any> {
  const url = utpl('https://api.coindesk.com/v1/bpi/historical/close.json?currency=EUR&start={start}&end={end}')
    .fillFromObject({
        start: moment(from).format('YYYY-MM-DD'),
        end: moment(to).format('YYYY-MM-DD')
      });
  return fetchJson(new URL(url));
}



